:root{
    --dark: #fff;
    --dark1 : #1a1b21;
    --dark2 : #161619;
    --darksub0: #F3BF99;
    --darksub1: #F5F5F5;
    --darksub2: #DCDCDC;
    --light: #000;
    --light1: #F5F5F5;
    --light2: #D5D4D8;
    --lightsub0: #F47D27;
    --lightsub1: #2B283A;
    --lightsub2: #4A4E74;
    /* replace variables names dark with light from below*/
}



/* *****************defaults************ */
a{
    text-decoration: none;
    margin: 0;
    padding: 0;
}


body{
    display: flex;
    justify-content: center;
    align-items: center;
    /* width: 100vw;
    height: 100vh; */
}

.container{
    /* border: 1px solid red;*/
    /* background-color: #161619;  */
    border-radius: 13px;
    /* width: 550px;
    height: 868px;
    display: block; */
    top: 30%;
}

.App{
    width: 317px;
    /* height: 780px; */
}

/* **********HEADER************ */

.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--light);
    background-color: var(--light1);
    border-radius: 12px 12px 0px 0px;
}

.header img{
    border-radius: 10px 10px 0px 0px;
    width: 317px;
    height: 317px;
}

.header .header--title{
    margin: 21px auto 0;
    font-family: 'Inter', 'sans-serif';
    font-size: 25px;
    line-height: 30px;
}
.header .header--subtitle{
    margin: 6.19px  auto 0;
    font-family: 'Inter', 'sans-serif';
    font-weight: 400;
    font-size: 12.8px;
    line-height: 15px;
    color: var(--lightsub0);
}
.header .header--web{
    margin: 8.81px auto 0 ;
    font-family: 'Inter', 'sans-serif';
    font-style: normal;
    font-weight: 400;
    font-size: 10.24px;
    line-height: 150%;
    color: var(--lightsub1);
}

/* ***********BUTTONS***************** */

.buttons{
    background-color: var(--light1);
    position: relative;
    border: none;
    color: var(--light);
    display: flex;
    text-align: center;
    align-items: center;
    justify-content:space-evenly;
    padding: 5px 0  15px;
    height: 82px;
}

.buttons > *{
    width: 115px;
    border-radius: 6px;
    padding: 9px 12px;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 8px;
    font-family: 'Inter';
    font-weight: 500;
    font-size: 14px;
    /* line-height: 16px; */
}

.buttons .buttons-email{
    background-color: white;
    color: #374151;
    border: 1px solid #D1D5DB;
    
}
.buttons .buttons-email img{
    margin: auto 0;
}

.buttons .buttons-linkedin{
    background-color: #5093E2;
    color: white;
}

.buttons .buttons-linkedin img{
    margin: auto 0;
}


/* **********main***********8 */

.main{
    background-color: var(--light1);
    padding: 0 36px 24px 36px;
    margin: 0;
    color: var(--lightsub1);
    display: flex;
    flex-direction: column;
    gap: 14px;
}

.main .main--title{
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    /* or 24px */
    margin: 0;
}

.main p{
    font-family: 'Inter', sans-serif;
    font-size: 10.24px;
    line-height: 150%;
    /* or 15px */
    color: var(--lightsub2);
}

/* ***********footer************** */

.footer{
    color: white;
    /* width: 317px; */
    height: 64px;
    background: var(--light2);
    padding: 0 50px;
    border-radius: 0px 0px 10px 10px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

